.banner {
  width: 100%;
  height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 0 50px;
}

.banner-title {
  padding-top: 200px;
  font-size: 1.3rem;
  color: #fff;
}

.banner-title span {
  font-size: 6rem;
  color: #d84315;
  font-weight: bold;
}

.description {
  font-size: 1.3rem;
  color: #fff;
}

.fa-arrow-down {
  color: #fff;
  position: absolute;
  bottom: 15%;
  right: 50%;
  font-size: 2rem;
  animation: arrow-down 2s linear infinite;
}

@keyframes arrow-down {
  0% {
    opacity: 0;
    transform: translateY(0);
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translateY(80px);
  }
}
