.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 0 50px;
  position: fixed;
  z-index: 99;
}

.active {
}

.logo {
  font-size: 2.3rem;
}

.logo i {
  color: red;
}

.header-items {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 30px;
  position: relative;
}

.header-items li {
  cursor: pointer;
  transition: all 0.3s linear;
  color: #ccc;
  text-transform: uppercase;
  font-weight: bold;
  transition: all 0.5s ease;
}

.header-items li.active {
  color: red;
}

.contact-logo {
  cursor: pointer;
  display: flex;
  gap: 10px;
  font-size: 1.5rem;
}

.contact-logo i {
  transition: all 0.3s linear;
  color: #ccc;
}

.contact-logo i:nth-child(1):hover {
  color: #fff;
}
.contact-logo i:nth-child(2):hover {
  color: #fff;
}
.contact-logo i:nth-child(3):hover {
  color: #fff;
}
