* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  color: #000060;
}

li {
  list-style: none;
  cursor: pointer;
}

a {
  text-decoration: none;
  color: #000;
}

.homeScreen {
  display: flex;
  padding: 50px 50px;
}

.items {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  width: 80%;
  gap: 20px;
  padding-right: 30px;
}
